:root {
	--nav-font: 'Dancing Script', cursive;
}

* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

.nav {
	text-transform: lowercase;
	font-size: 1.8em;
	grid-area: nav;
	color: #d3d3d3;
    background: -webkit-linear-gradient(to bottom left, black, rgb(70,70,70));
    background: -o-linear-gradient(to bottom left, black, rgb(70,70,70));
    background: -webkit-gradient(linear, right top, left bottom, from(black), to(rgb(70,70,70)));
    background: -webkit-linear-gradient(top right, black, rgb(70,70,70));
    background: -o-linear-gradient(top right, black, rgb(70,70,70));
    background: linear-gradient(to bottom left, black, rgb(70,70,70));
	background-size: 100% 100%;
	border-right: 13px solid pink;
	border-left: 13px solid pink;
	display: grid;
	grid-template-rows: 3fr 3fr 1fr 1fr 1fr 1fr 1f70,70,70r 1fr 5fr;
	-ms-flex-align: start;
	    align-items: start;	
	-ms-flex-pack: center;	
	    justify-content: center;			
}

.name {
	font-family: 'Limelight', cursive;
	font-size: 1.1em;
	justify-self: center;
	cursor: pointer;
}

.animals {
	font-family: var(--nav-font);
	justify-self: center;
	cursor: pointer;
}

.culture {
	font-family: var(--nav-font);
	justify-self: center;
	cursor: pointer;
}
.nature {
	font-family: var(--nav-font);
	justify-self: center;
	cursor: pointer;
}

.hands {
	font-family: var(--nav-font);
	justify-self: center;
	cursor: pointer;
}

.sundial {
	font-family: var(--nav-font);
	justify-self: center;
	cursor: pointer;
}

.about {
	font-family: var(--nav-font);
	justify-self: center;
	cursor: pointer;
}



