* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

.appBody {
	grid-area: body;
    background: -webkit-linear-gradient(to bottom right, #d3d3d7, white);
    background: -o-linear-gradient(to bottom right, #d3d3d7, white);
    background: -webkit-gradient(linear, left top, right bottom, from(#d3d3d7), to(white));
    background: -webkit-linear-gradient(top left, #d3d3d7, white);
    background: -o-linear-gradient(top left, #d3d3d7, white);
    background: linear-gradient(to bottom right, #d3d3d7, white);
	display: grid;
	grid-template-areas:
						/*12x10*/
						'w1 w1 w1 w1 w1 w1 w1 w1 w1 w1'
						'w2 i  i  i  i  i  i  i  i  w3'
						'w2 i  i  i  i  i  i  i  i  w3'
						'w2 i  i  i  i  i  i  i  i  w3'
						'w2 i  i  i  i  i  i  i  i  w3'
						'w2 i  i  i  i  i  i  i  i  w3'
						'w2 i  i  i  i  i  i  i  i  w3'
						'w2 i  i  i  i  i  i  i  i  w3'
						'w2 i  i  i  i  i  i  i  i  w3'
						'w2 i  i  i  i  i  i  i  i  w3'
						'w2 i  i  i  i  i  i  i  i  w3'
						'w4 w4 w4 w4 w4 w4 w4 w4 w4 w4'

}

.i {
	grid-area: i;
	position: relative;
	background-repeat: no-repeat;
	border-radius: 6px;
	border: 13px solid white;
	-webkit-box-shadow: 8px 8px 18px gray;
	        box-shadow: 8px 8px 18px gray;
}

.wsp1 {
	grid-area: w1;
}

.wsp2 {
	grid-area: w2;
}

.wsp3 {
	grid-area: w3;
}

.wsp4 {
	grid-area: w4;
}

.next {
	opacity: .65;
	cursor: pointer;
	top: 50%;
	right: 5%;
  	width: 0; 
  	height: 0; 
  	border-top: 15px solid transparent;
  	border-bottom: 15px solid transparent; 
  	border-left:15px solid black; 
	z-index: 1;
	position: absolute;
}

.next:hover {
	opacity: .4;
}

.last {
	opacity: .65;
	cursor: pointer;
	top: 50%;
	right: 75%;
  	width: 0; 
  	height: 0; 
  	border-top: 15px solid transparent;
  	border-bottom: 15px solid transparent; 
  	border-right:15px solid black; 
	z-index: 1;
	position: absolute;
}

.last:hover {
	opacity: .4;
}


