* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

.appGrid {
	display: grid;
	height: 100vh;
	grid-template-areas: 'nav body';
	grid-template-columns: 1.2fr 5fr;
	grid-template-rows: 100%;
}

